@use 'base/variables' as v;

.formulario{
    margin-top:5rem;

    .campo{
        display:flex;
        align-items: center;
        margin-bottom:3rem;

        label{
            flex: 0 0 9rem;
        }   

        input{
            flex:1;
            padding:0.8rem;
            border:none;
            background-color: lighten(v.$negro, 88%);
            border-radius: 0.8rem;
        }
    }
}

