@use 'base/variables' as v;
@use 'base/mixins'as m;

@media (prefers-color-scheme: dark){
    $grisOscuro : #2c2c3b;
    body{
        background-color:$grisOscuro;
    }

    .tagline{
        color:v.$blanco;
    }

    .descripcion-pagina{
        color:v.$grisClaro;
    }

    .formulario label,
    .formulario .campo input,
    .nombre-pagina{
        color:darken(v.$blanco, 15%);
        

    }

    .formulario .campo input{
        background-color:lighten($grisOscuro, 15%);
        &::placeholder{
            color:darken(v.$grisClaro, 5%);
        }
    }

    .login .acciones a{
        @include m.aclarar(#0891b2, true);
    }
    .crear .acciones a{
        @include m.aclarar(#7c3aed, true);
    }
    .forget .acciones a{
        @include m.aclarar(#db2777, true);
    }

    .alerta.error{
        color:lighten(#a90000, 10%);
    }

    
    .sidebar{
        @include m.tablet{ 
            background-color:darken($grisOscuro, 5%);

            .sidebar-nav a{
                &.activo,
                &:hover{
                    background-color:v.$naranja;
                }                
            }
        }
    }
   

    .barra-mobile{
        background-color:darken($grisOscuro, 15%);
    }

    .barra{
        box-shadow: none;
        background-color: darken($grisOscuro, 10%);
        p{
            color:v.$grisClaro;
            span{
                color:v.$naranja;
            }
        }

        .cerrarSesion{
            @include m.boton(v.$naranja, v.$blanco);
            padding:1rem;
        }
    }

    .modal{ 
        
        .formulario{
            background-color:darken($grisOscuro, 5%);
            legend{
                color:v.$blanco;
            }
            
            .campo{
                label{
                    color:v.$blanco;
                }
                input[type="text"]{
                    background-color:lighten($grisOscuro, 5%);
                    border:none;
                    color:v.$blanco;

                    &::placeholder{
                        color:darken(v.$grisClaro, 30%);
                    }
                }
            }
        }  
    }


    .filtros{
        background-color:lighten($grisOscuro, 10%);
        h2,
        label{
            color:v.$blanco;
        }
    }

    .filtros .campo input[type="radio"]{
        accent-color: v.$naranja
    }
    

    //comenzaremos con la parte de nuestro dashboard y sus dem√°s
    // partes importantes que le complementan.
    .dashboard{
        background-color:$grisOscuro;
    }

    .dashboard .formulario .campo input:not([type="submit"]){
        background-color:lighten($grisOscuro, 5%);
        color:v.$blanco;
        border:none;

        &::placeholder{
            color:darken(v.$grisClaro, 30%);
        }
    }

    .listado-proyectos .proyecto{
        background-color:lighten($grisOscuro, 20%);
        &:hover{
            background-color:lighten($grisOscuro, 25%);
        }
    }

    .listado-tareas .tarea{
        background-color:lighten($grisOscuro, 10%);
        border:none;
        margin-bottom: 2.5rem;

        &:first-of-type{
            margin-top: 10rem;
        }
        &:last-of-type{
            margin-bottom: 0;
        }
        p{
            color:v.$grisClaro;
        }
    }
    .listado-tareas .no-tareas{
        color:v.$blanco;
    }

    .dashboard .enlace{
        background-color:lighten($grisOscuro, 20%);
        color:v.$grisClaro;

        &:hover{
            color:darken(v.$grisClaro, 20%);
        }
    }
    .dashboard .formulario input:not([type="submit"]){
        color:v.$negro;
    }
}