@use 'base/variables' as v; 
@use 'base/mixins' as m; 

.sidebar{
    opacity: 0;
    background-color:v.$indigo;
    height: 0;
    overflow: hidden;
    transition-property:opacity;
    transition-duration:30ms;
    transition-timing-function: ease-in;

    &.mostrar{
        opacity: 1;
        padding:2rem;
        height: 130%;
        position:absolute;
        top:0;
        bottom:0;
        right: 0;
        left: 0;
        background-color: rgb(0 0 0 / .95);
        @include m.tablet{
            display: none;
        }
    }
    &.ocultar{
        opacity: 0;
    }

    @include m.tablet{
        padding:2rem;
        opacity: 1;
        background-color: v.$indigo;
        height: 100%;
    }

    .contenedor-sidebar{
        h2{
            padding-bottom:2rem;
        }

        @include m.tablet{
            h2{padding: 0;}
        }

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap:5rem;
        .cerrar-menu{
            img{
                width: 3.0rem;
                cursor: pointer;
            }
            @include m.tablet{
                display:none;
            }
        }
    }

    h2{
        color:v.$blanco;
        margin:0;
        text-align:center;
    }

    .sidebar-nav{
        margin-top:4rem;
        padding-top: 4rem;
        a{
            display:block;
            color:v.$blanco;
            padding:.7rem;
            transition-property:background-color;
            transition-duration:360ms;
            font-weight: bold;
            text-transform:uppercase;
            font-size:1.8rem;
            margin-bottom:3rem;
            text-align:center;

            &:hover,
            &.activo{
                background-color: darken(v.$indigo, 20%);
                border-radius:.8rem;
            }

            @include m.tablet{
                //estilos para tablets y adelante.
                font-size: 1.7rem;
                text-align: left;
                margin-bottom: 2rem;
            }
        }
    }

    .cerrar-sesion-mobil{
        margin-top:10rem;

        .cerrarSesion{
            @include m.boton(v.$indigo, v.$blanco);
        }

        @include m.tablet{
            display:none;
        }
    }
}
