@use 'base/variables' as v;
@use 'base/mixins' as m;

.modal{
    position:absolute;
    background-color: rgb(0 0 0 / 0.8);
    top:0;
    right: 0;
    bottom: 0;
    left: 0;

    .formulario{
        //estilo para nuestro formulario.
        max-width: 60rem;
        margin: 0 auto;
        text-align: center;
        background-color:v.$blanco;
        padding:2rem;
        border-radius: .5rem;
        transform: translateY(-30rem);
        transition-property:all;
        transition-duration:500ms;
        transition-timing-function:ease-in;
        opacity: 0;

        legend{
            font-size:2rem;
            text-align:center;
            display:block;
            margin-bottom:2rem;
            font-weight:bold;
        }

        input[type="text"]{
            border:.3px solid rgb(0 0 0 / .2);
        }

        .opciones{
            display:flex;
            justify-content: space-between;
            gap:1rem;
            .submit-nueva-tarea{
                @include m.boton(v.$indigo, v.$blanco);
            }

            .cerrar-modal{
                @include m.boton(v.$naranja, v.$blanco);
                border-radius: 0.5rem;;
            }
        }

        &.animation{
            position:relative;
            top:50%;
            transform: translateY(-50%);
            opacity: 1;
        }
        &._cerrar{
            //efectos especiales al cerrar modal.
            transform: translateY(-200%);
            opacity: 0;
        }
    }

}