@use 'base/variables' as v;
@use 'base/mixins' as m;


.contenedor-nueva-tarea{
    display:flex;
    justify-content: center;
    
    .agregar-tarea{
        @include m.boton(v.$cyan, v.$blanco);
    }    
}


.listado-tareas{
    list-style-type:none;
    padding:0;
    margin-top: 3rem;
    .no-tareas{
        text-align:center;
        text-decoration: underline;
    }
}

.tarea{
    padding:.5rem 2rem;
    border-bottom:1px solid rgb( 0 0 0 / 0.3);
    display:flex;
    justify-content:space-between;
    align-items: center;
    &:last-of-type{
        border-bottom: 0px;
    }

    p{
        font-size:1.5rem;
        font-weight: 700;
        // text-transform: uppercase;
    }

    .opciones{
        display:flex;
        gap:2rem;
        button{
            border:none;
            font-size: 1.3rem;
            padding:.5rem 1rem;
            text-transform: uppercase;
            font-weight: 700;
            border-radius: 1rem;

            &:hover{
                cursor: pointer;
                
            }

        }
        
        .completo{
            background-color: v.$azul;
            color:v.$blanco;
        }
        .pendiente{
            background-color: v.$naranja;
            color:v.$blanco;
        }

        .eliminar-tarea{
            background-color: v.$rojo;
            color:v.$blanco;
        }
    }
}