@use 'base/variables' as v;
@use 'base/mixins' as m; 


.barra-mobile{
    background-color:v.$indigo;
    padding:2rem 4rem;
    display:flex;
    justify-content: space-between;
    align-items:center;
    @include m.tablet{
        display: none;
    }
    h1{
        margin:0;
        color:v.$blanco;
    }

    .menu{
        img{
            width: .8rem;
            cursor: pointer;
        }
    }
}

.barra{
    display:none;
    
    @include m.tablet{
        background-color:v.$blanco;
        box-shadow:-1px 10px 5px 0px rgba(125, 125, 125, 0.75);
        padding:2rem;
        display:flex;
        justify-content: space-between;
        align-items: center;
    }
    p{
        margin:0;
       
        span{
            color:v.$indigo;
            font-weight: 700;
        }
    }

    .cerrarSesion{
        @include m.boton(darken(v.$indigo, 10%), v.$blanco);
        font-size:1.5rem;
        padding:1.5rem;
        border-radius:.9rem;
    }
}