@use 'base/variables' as v;
@use 'base/mixins' as m;

.crear{
    margin-top:5rem;
    @include m.TextoDegradado(v.$morado);

    
    input[type="submit"]{
        @include m.boton(v.$morado, v.$blanco);
    }

    .acciones{
        a{
            color: v.$morado;
        }
    }
}