@use 'base/variables'as v;
@use 'base/mixins'as m;

.dashboard{
    background-color:v.$grisClaro;

    @include m.tablet{
        display:flex;
        height:100vh;
    }

    .principal{
        flex:1; 
        overflow: scroll;
    }

    .contenido{
        padding:2rem;
        .nombre-pagina{
            font-size:2.9rem;
            text-align:center;
        }
    }

    .formulario{ 
        input:not([type="submit"]){
            background-color:v.$blanco;
        }

        input[type="submit"]{ 
            @include m.boton(v.$azul,v.$blanco);
            border-radius:.5rem;
            font-size:1.5rem;
            text-transform:uppercase;
        }
    }

    .enlace{
        color:lighten(v.$indigo, 10%);
        font-weight:bold;
        background-color: lighten(v.$gris, 45%);
        padding:1.2rem;
        border-radius:.9rem;
        text-transform: lowercase;

        &:hover{
            color:darken(v.$indigo,15%)
        }
    }
}
